<template>
  <div class="slider_block">
    <carousel
      class="slider"
      :items="1"
      :autoplay="true"
      :nav="false"
      :dots="true"
      :loop="true"
    >
      <img
        v-for="item in banner_block"
        :key="item.id"
        :src="require('@/assets' + item.pic)"
      />
    </carousel>
    <div class="go_to" @click="toNews()"></div>
  </div>
</template>

<script>
import carousel from 'vue-owl-carousel'

export default {
  props: {
    banner_block: Array,
  },
  components: {
    carousel,
  },
  data() {
    return {}
  },
  methods: {
    toNews() {
      let news = document.querySelector('#news')
      window.scroll({
        top: news.offsetTop,
        behavior: 'smooth',
      })
    },
  },
}
</script>

<style>
.slider_block {
  position: relative;
}
.slider_block .slider .owl-dots {
  position: absolute;
  right: 20px;
  bottom: 10%;
}
.slider_block .slider .owl-dots .owl-dot {
  background-color: inherit;
  width: 20px;
  height: 20px;
  opacity: 1;
  margin: 6px;
  border: 1px solid #fff;
  border-radius: 100%;
  display: block;
  outline: none;
  margin: 12px;
}
.slider_block .slider .owl-dots .owl-dot span {
  display: none;
}
.slider_block .slider .owl-dots .owl-dot:hover,
.slider_block .slider .owl-dots .owl-dot.active {
  background-color: #fff;
}
.slider_block .go_to {
  position: absolute;
  left: calc(50% - 40px);
  bottom: -40px;
  width: 80px;
  height: 80px;
  background-color: #555555;
  border-radius: 100%;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 2;
}
.slider_block .go_to::after {
  content: '';
  background-image: url(~@/assets/img/Group_25.png);
  background-size: 100% 100%;
  display: block;
  width: 28px;
  height: 30px;
  transition-duration: 0.3s;
}
.slider_block .go_to:hover::after {
  transform: translateY(3px);
}
@media (max-width: 992px) {
  .slider_block .slider .owl-dots {
    bottom: 10%;
    right: 16px;
  }
  .slider_block .slider .owl-dots .owl-dot {
    width: 10px;
    height: 10px;
    margin: 6px;
  }

  .slider_block .go_to {
    left: calc(50% - 18px);
    bottom: -18px;
    width: 36px;
    height: 36px;
  }
  .slider_block .go_to::after {
    width: 14px;
    height: 14px;
  }
}
</style>
